                  @click="vehicleCloseDelete"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-3"
                  text
                  @click="deleteItemConfirm"
                >
                  Sim
                </v-btn>

                <v-spacer />
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>

      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          id="editBtn"
          small
          class="mr-2"
          @click="editVehicle(item), disabledField(), (showVehicleCloneBtn = true), maskCheckbox(), setEditTime()"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          id="addBtn"
          class="mr-2"
          @click="addLicence(item), greaterLicenceId(), getVehicleID(item), editedLicence.STATUS = 'PRE-PEDIDO'"
        >
          mdi-text-box-plus-outline
        </v-icon>
        <v-icon
          id="newLicenceBtn"
          v-show="false"
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>

      <template v-slot:no-data>
        <v-btn
          color="blue"
          dark
          @click="fetchFleet"
        >
          Carregar Lista
        </v-btn>
      </template>

      <template v-slot:expanded-item="{ licencesHeaders }">
        <v-data-table
          :headers="licencesHeaders"
          :items="licences"
          :search="search"
          :footer-props="{ itemsPerPageOptions: [10, 15, 30] }"
          item-key="ID"
          sort-by="ID"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-spacer />
              <v-text-field
                v-model="search"
                color="blue"
                append-icon="mdi-magnify"
                label="Procurar"
                hide-details
              />
              <v-spacer />

              <v-dialog
                id="new item section"
                v-model="licenceDialog"
                max-width="50vw"
              >
              </v-dialog>
            </v-toolbar>
          </template>
        </v-data-table>
      </template>
    </v-data-table>
  </div>
</template>

<script>
  import axios from 'axios'
  import Vue from 'vue'

  export default {
    name: 'FleetControl',

    filters: {
      currencyBRL (valor) {
        return valor.toLocaleString('pt-BR', {
          style: 'currency',
          currency: 'BRL',
        })
      },
    },

    data: () => ({
      vId: null,
      lId: null,
      repeatedV: false,
      repeatedL: false,
      erroCep: false,
      valid: true,
      checkboxAtivo: false,
      checkboxNF: false,
      vehicleDialog: false,
      vehicleDialogSave: false,
      vehicleDialogDelete: false,
      licenceRegisterDialog: false,
      licenceWatchDialog: false,
